<template>
  <div>
    <div class="header">
      <div class="title">
        <router-link to="/">
          <i class="el-icon-refresh-left left"></i>
        </router-link>
        <router-link to="/search">
          <i class="el-icon-search right"></i>
        </router-link>
      </div>
    </div>
    <div class="list clearfix">
        <router-link 
        tag="div"
        :to="`/player/${item.id}/${item.name}/${setUrl(item.al.picUrl)}`" 
        class="item" 
        v-for="(item, index) in detailsDate" 
        :key="index"> 
          <div class="img-warpper">
            <img :src="item.al.picUrl" />
          </div>
          <div class="main">{{ item.al.name }}</div>
          
        </router-link>
    </div>
  </div>
  
</template>

<script>
export default {
  name:"Details",
  data() {
    return {
      detailsDate:[]
    }
  },
  mounted(){
    this.$api.getMusicDetails({
      id:this.$route.params.id
    }).then((res) => {
      console.log(res)
        this.detailsDate = res.playlist.tracks;
        
      });
  },
  methods:{
    setUrl(url){
      if (url) {
        // 编码
        return encodeURIComponent(url)
      }
    }
  }
}
</script>

<style scoped>
.header {
  padding: 20px;
}
.title {
  display: flex;
  justify-content: space-between;
}
i {
  font-size: 30px;
}
.list {
  padding: 20px;
  background: rgba(250, 218, 218, 0.2);
}
.list .item {
  float: left;
  width: 22%;
  padding: 0 5px;
  height: 120px;
}

.main {
  margin-top: 12px;
  font-size: 12px;
  /* 取消换行 */
  white-space: nowrap;
  /* 隐藏溢出部分 */
  overflow: hidden;
  /* 超出用...表示 */
  text-overflow: ellipsis;
}

</style>